import React, { useState } from 'react';
import ReservationForm from '../components/ReservationForm';
import Sidebar from '../components/Sidebar';

const Agendamento: React.FC = () => {
  // Dados mockados
  const availableTimes = [
    { id: 1, time: '10:00', available: true },
    { id: 2, time: '11:00', available: true },
    { id: 3, time: '12:00', available: false },
    { id: 4, time: '14:00', available: true },
  ];

  const environments = [
    { id: 1, name: 'Biblioteca', description: 'Ambiente silencioso para leitura', icon: '📚', capacity: '50 pessoas', equipment: 'Wi-Fi, mesas de estudo' },
    { id: 2, name: 'Salas de Aula', description: 'Salas para aulas e reuniões acadêmicas', icon: '🖊️', capacity: '30 pessoas', equipment: 'Projetor, quadro branco' },
    { id: 3, name: 'AlphaLAB', description: 'Espaço para inovação e prototipagem', icon: '💡', capacity: '15 pessoas', equipment: 'Computadores, impressora 3D' },
    { id: 4, name: 'Auditório', description: 'Espaço para palestras e eventos', icon: '🎤', capacity: '200 pessoas', equipment: 'Microfone, som, telão' }
  ];

  const [selectedEnvironment, setSelectedEnvironment] = useState<number | null>(null);
  const [selectedTime, setSelectedTime] = useState<number | null>(null);
  const [reservations, setReservations] = useState<{ date: string; time: string; environment: string }[]>([]);
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [errorMessage, setErrorMessage] = useState<string | null>(null);

  const handleReserve = () => {
    if (selectedTime && selectedEnvironment) {
      const newReservation = {
        date: new Date().toLocaleDateString(), // Adicionando data da reserva
        time: availableTimes.find(time => time.id === selectedTime)?.time || '',
        environment: environments.find(env => env.id === selectedEnvironment)?.name || '',
      };
      setReservations([...reservations, newReservation]);
      setSuccessMessage('Reserva realizada com sucesso!');
      setErrorMessage(null);
    } else {
      setErrorMessage('Selecione um horário e ambiente.');
      setSuccessMessage(null);
    }
  };

  const handleCancel = (index: number) => {
    const updatedReservations = reservations.filter((_, i) => i !== index);
    setReservations(updatedReservations);
    setSuccessMessage('Reserva cancelada com sucesso!');
    setErrorMessage(null);
  };

  return (
    <div style={{ display: 'flex' }}>
      <Sidebar />
      <div style={{ marginLeft: '250px', padding: '20px', width: 'calc(100% - 250px)' }}>
        <h1>Agendar Reserva</h1>

        {/* Seleção de Ambiente */}
        <div style={{ display: 'flex', gap: '20px', flexWrap: 'wrap' }}>
          {environments.map((env) => (
            <div
              key={env.id}
              onClick={() => setSelectedEnvironment(env.id)}
              style={{
                cursor: 'pointer',
                padding: '20px',
                border: `2px solid ${selectedEnvironment === env.id ? 'blue' : 'gray'}`,
                borderRadius: '8px',
                textAlign: 'center',
                width: '150px',
                transition: 'all 0.3s ease',
                boxShadow: selectedEnvironment === env.id ? '0 4px 10px rgba(0, 0, 255, 0.2)' : '',
                backgroundColor: selectedEnvironment === env.id ? '#e6f0ff' : '#fff',
              }}
              title={env.description}
            >
              <div style={{ fontSize: '30px' }}>{env.icon}</div>
              <h3>{env.name}</h3>
              <p>{env.description}</p>
              <p><strong>Capacidade:</strong> {env.capacity}</p>
              <p><strong>Equipamento:</strong> {env.equipment}</p>
            </div>
          ))}
        </div>

        {/* Exibe os horários disponíveis para o ambiente selecionado */}
        {selectedEnvironment && (
          <div style={{ marginTop: '20px' }}>
            <h3>Horários Disponíveis</h3>
            <div style={{ display: 'flex', gap: '10px' }}>
              {availableTimes.map((time) => (
                <button
                  key={time.id}
                  onClick={() => setSelectedTime(time.id)}
                  style={{
                    padding: '10px',
                    border: '2px solid gray',
                    borderRadius: '5px',
                    backgroundColor: time.available ? (selectedTime === time.id ? '#66cc66' : '#fff') : '#f2f2f2',
                    cursor: time.available ? 'pointer' : 'not-allowed',
                    opacity: time.available ? 1 : 0.5,
                  }}
                  disabled={!time.available}
                >
                  {time.time}
                </button>
              ))}
            </div>
            <button
              onClick={handleReserve}
              style={{
                marginTop: '20px',
                padding: '10px 20px',
                backgroundColor: '#4caf50',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer',
              }}
            >
              Confirmar Reserva
            </button>
            {successMessage && <p style={{ color: 'green', marginTop: '10px' }}>{successMessage}</p>}
            {errorMessage && <p style={{ color: 'red', marginTop: '10px' }}>{errorMessage}</p>}
          </div>
        )}

        {/* Lista de reservas realizadas */}
        <div style={{ marginTop: '30px' }}>
          <h3>Reservas Realizadas</h3>
          {reservations.length > 0 ? (
            <ul>
              {reservations.map((res, index) => (
                <li key={index} style={{ marginBottom: '10px' }}>
                  <div>
                    <strong>Data:</strong> {res.date} <strong>Horário:</strong> {res.time} <strong>Ambiente:</strong> {res.environment}
                  </div>
                  <button
                    onClick={() => alert('Reserva editada com sucesso')}
                    style={{ marginRight: '10px', padding: '5px 10px', cursor: 'pointer' }}
                  >
                    Editar
                  </button>
                  <button
                    onClick={() => handleCancel(index)}
                    style={{ padding: '5px 10px', cursor: 'pointer', backgroundColor: 'red', color: 'white' }}
                  >
                    Cancelar
                  </button>
                </li>
              ))}
            </ul>
          ) : (
            <p>Você ainda não tem reservas realizadas.</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default Agendamento;
